import React from "react";
import {MenuList, MenuItem} from "@material-ui/core";

import { withStyles } from "@material-ui/core";

import settings from "../../../settings";
import {PopperWrap} from '..';

let styles = theme=>({
    
});

let TODO = {
    TODO:[
        "1. 现在只写了弹出的效果(分享, 更多)"
    ]
}

class Popper extends React.Component{

    resolveSettings = (typo)=>{
        let val = [];
        switch(typo){
            case "more":
                val = settings.menuitem.more;
                break;
            case "share":
                val = settings.menuitem.share;
                break;
            default:
                val.push(
                    {
                        "title":"none"
                    }
                );
        }
        return val.map((item, index)=>{
            return <MenuItem onClick={this.handleClick.popperclick} key={index} id={`${item.title}`}>
                {item.title}
            </MenuItem>
        });
    }

    render(){
        let {classes, typo, anchorE1, popper} = this.props;
        return <PopperWrap
            id="poppermenu"
            anchorEl={anchorE1}
            popper={popper}
            popperclose={this.handleClick.popperclose}
        >
            <MenuList>
                {this.resolveSettings(typo)}
            </MenuList>
        </PopperWrap>
    }
    
    handleClick= {
        popperclose: (e)=>{
            if (this.props.anchorE1.contains(e.target)) {
                return;
            }
            this.props.callBack();
        },
        popperclick: (e)=>{
            let {id} = e.target;
            console.log("popper", id);
            this.handleClick.popperclose(e);
        }
    }
}

export default withStyles(styles)(Popper);