import React, {Fragment} from "react";
import ThumbUp from '@material-ui/icons/ThumbUpRounded';
import ThumbDown from '@material-ui/icons/ThumbDownRounded';
import Star from '@material-ui/icons/StarRounded';
import SentimentVerySatisfied from '@material-ui/icons/SentimentVerySatisfiedRounded';
import MoreHoriz from '@material-ui/icons/MoreHorizRounded';
import Forum from '@material-ui/icons/ForumRounded';

import {Button, IconButton} from '@material-ui/core';

import ShareButton from './Share';
import Portals from './Portals';
import Comment from './Comment/';
import {PopperMenu} from '../../../../Components';

import { withStyles } from '@material-ui/core';

let styles = theme=>({
    //按钮
    actions:{
        display: 'flex',
        padding: '4px 16px',
        alignItems: 'center',
        '& > :not(:first-child)':{
            marginLeft: '12px'
        }
    }
});

class Buttons extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            portal: false,
            variant: {
                agree: false,
                disagree: false
            },
            popper: false,
            comment: false,
            popperTypo: "",
        }
    }
    handleClick = {
        agree: (e)=>{
            //赞同
            this.setState({
                variant: {
                    agree: !this.state.variant.agree,
                    disagree: false,
                }
            });
        },
        disagree: (e)=>{
            //不赞同
            this.setState({
                variant: {
                    agree: false,
                    disagree: !this.state.variant.disagree,
                }
            });
        },
        comment: (e)=>{
            //评论
            this.setState({
                comment: !this.state.comment,
            })
        },
        collection: (e)=>{
            //收藏
            this.setState({
                portal: true,
            })
        },
        thanks: (e)=>{
            //感谢
        },
        more: (e)=>{
            //更多
            this.anchorPopper = e.currentTarget;
            this.setState({ popper: !this.state.popper, popperTypo: "more" })
        },
        portalclose: (e)=>{
            this.setState({
                portal: false
            });
        },
    }
    render(){
        let {classes, info} = this.props;
        let typeDipose = this.typeDipose(info.type);
        let likeDipose = this.likeDipose(info.like);
        let commentDipose = this.commentDipose(info.comment);
        let {popper, variant, portal, comment} = this.state;

        return <Fragment>
            <div className={classes.actions}>
                <Button variant={variant.agree ? "contained" : "outlined"} color="primary" onClick={this.handleClick.agree}>
                    <ThumbUp />
                    赞同 {likeDipose}
                </Button>
                <Button variant={variant.disagree ? "contained" : "outlined"} color="primary" onClick={this.handleClick.disagree}>
                    <ThumbDown />
                </Button>
                <Button mini color="primary" onClick={this.handleClick.comment}>
                    <Forum /> {commentDipose} 条评论
                </Button>
                <ShareButton />
                <Button mini color="primary" onClick={this.handleClick.collection}>
                    <Star /> 收藏
                </Button>
                {typeDipose}
                <IconButton
                    mini="true" color="primary"
                    aria-owns={popper ? 'poppermenu' : undefined}
                    aria-haspopup="true"
                    onClick={this.handleClick.more}
                >
                    <MoreHoriz />
                </IconButton>
            </div>

            <Comment
                count={info.comment}
                display={comment}
            />

            {/* 弹出菜单(更多) */}
            <PopperMenu
                typo={this.state.popperTypo}
                anchorE1={this.anchorPopper}
                popper={popper}
                callBack={()=>this.setState({popper: false})}
            />

            {/* 模态框(收藏) */}
            <Portals open={portal} handleClose={this.handleClick.portalclose}/>
        </Fragment>
    }

    /**
     * 显示赞同数量
     */
    likeDipose = (like)=>{
        let m = Math.floor(like/10000);
        let k = Math.floor(like/1000);
        if(m > 0)
            like = `${m}w`;
        else if(k > 0)
            like = `${k}k`;
        return like
    }
    /**
     * 显示讨论内容
     */
    commentDipose = (comment)=>{
        let k = Math.floor(comment/1000);
        if(k > 0)
            comment = `${k}k`;
        return comment;
    }
    /**
     * 显示: 文章-举报; 回答-感谢
     */
    typeDipose = (type)=>{
        let icon;
        let str;
        switch(type){
            case "answer":
                icon = <SentimentVerySatisfied />
                str = "感谢";
                break;
            case 'column':
                str = "举报";
                break;
            default:
                break;
        }
        return <Button mini color="primary" onClick={this.handleClick.thanks}>
            {icon} {str}
        </Button>
    }
}
export default withStyles(styles)(Buttons);